import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import { userInfo } from "../../API/auth";
import {
    getEntertainerById,
    getCategory,
    updateEntertainer,
} from "../../API/utilities";
import { useParams } from "react-router-dom";

export default function UpdateEntertainer(props) {
    const [values, setValues] = useState({
        message: "",
        name: "",
        username: "",
        religion: "",
        tribe: "",
        boobs: "",
        category: "",
        availability: "",
    });
    const [categories, setCategories] = useState([]);

    const id = useParams().id;

    const {
        message,
        name,
        username,
        religion,
        tribe,
        boobs,
        category,
        availability,
    } = values;

    const formHandler = (e) => {
        setValues({
            ...values,
            [e.target.name]: e.target.value,
        });
    };

    const submitHandler = (e) => {
        e.preventDefault();
        const formData = new FormData(e.target);
        updateEntertainer(id, formData, userInfo().token)
            .then((result) => {
                setValues({
                    ...values,
                    message: "entertainer successfully updated!",
                });
            })
            .catch((err) => {
                setValues({ ...values, message: "something weng wrong!" });
            });
    };

    useEffect(() => {
        getEntertainerById(id, userInfo().token)
            .then((result) =>
                setValues({
                    ...values,
                    name: result.data.name,
                    username: result.data.username,
                    religion: result.data.religion,
                    tribe: result.data.tribe,
                    boobs: result.data.boobs,
                    category: result.data.category,
                    availability: result.data.availability,
                })
            )
            .catch((err) => console.log(err));
        getCategory(userInfo().token)
            .then((result) => setCategories(result.data))
            .catch((err) => console.log(err));
    }, []);

    return (
        <Layout title="Update Entertainer">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Update Entertainer</h1>
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {message && (
                            <div className="alert alert-success" role="alert">
                                {message}
                            </div>
                        )}
                        <form
                            onSubmit={submitHandler}
                            encType="multipart/form-data"
                        >
                            <div className="form-group mb-1">
                                <label for="name">Entertainer Name</label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="name"
                                    name="name"
                                    value={name}
                                    onChange={formHandler}
                                    placeholder="Enter Name"
                                />
                            </div>
                            <div className="form-group mb-1">
                                <label for="username">
                                    Entertainer Username
                                </label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="username"
                                    name="username"
                                    value={username}
                                    onChange={formHandler}
                                    placeholder="Enter Username"
                                />
                            </div>
                            <div className="form-group mb-1">
                                <label for="religion">Religion</label>
                                <select
                                    className="form-control"
                                    id="religion"
                                    name="religion"
                                    value={religion}
                                    onChange={formHandler}
                                >
                                    <option>muslim</option>
                                    <option>hindu</option>
                                    <option>christian</option>
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="tribe">Tribe</label>
                                <select
                                    className="form-control"
                                    id="tribe"
                                    name="tribe"
                                    value={tribe}
                                    onChange={formHandler}
                                >
                                    <option value={false}>Bangali</option>
                                    <option value={true}>Chakma</option>
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="boobs">Boobs Size</label>
                                <select
                                    className="form-control"
                                    id="boobs"
                                    name="boobs"
                                    value={boobs}
                                    onChange={formHandler}
                                >
                                    <option>large</option>
                                    <option>medium</option>
                                    <option>small</option>
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="category">Category</label>
                                <select
                                    className="form-control"
                                    id="category"
                                    name="category"
                                    value={category._id}
                                    onChange={formHandler}
                                >
                                    {categories.map((item) => (
                                        <option key={item._id} value={item._id}>
                                            {item.name}
                                        </option>
                                    ))}
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="availability">Availability</label>
                                <select
                                    className="form-control"
                                    id="availability"
                                    name="availability"
                                    value={availability}
                                    onChange={formHandler}
                                >
                                    <option value={false}>Not Available</option>
                                    <option value={true}>Available</option>
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="profile">Profile Photo</label>
                                <input
                                    type="file"
                                    className="form-control-file"
                                    id="profile"
                                    name="profile"
                                    onChange={formHandler}
                                />
                            </div>

                            <button type="submit" className="btn btn-primary">
                                Submit
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
