import React from "react";
import { useState } from "react";
import { Buffer } from "buffer";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleInfo, faTrash } from "@fortawesome/free-solid-svg-icons";

export default function PostCard({
    post,
    userId,
    deleteHandler,
    deleteCommentHandler,
    submitHandler,
}) {
    const [comment, setComment] = useState("");

    const formHandler = (e) => {
        setComment(e.target.value);
    };

    return (
        <div className="card mb-4">
            <div className="card-body">
                <div className="d-flex">
                    <h5 className="card-title me-3">
                        Post from: {post.user.email}
                    </h5>

                    {post.user._id === userId && (
                        <>
                            <Link
                                to={`/updatepost/${post._id}/`}
                                className="btn btn-dark btn-sm m-1"
                            >
                                <FontAwesomeIcon icon={faCircleInfo} />
                            </Link>
                            <button
                                type="button"
                                className="btn btn-danger btn-sm m-1 "
                                onClick={() => deleteHandler(post._id)}
                            >
                                <FontAwesomeIcon icon={faTrash} />
                            </button>
                        </>
                    )}
                </div>

                {post.name && (
                    <p className="card-text text-capitalize">
                        {post.name.name}
                    </p>
                )}
                <p className="card-text">{post.desc}</p>
                <div className="row">
                    {post.photos &&
                        post.photos.map((photo) => (
                            <div
                                key={Math.random()}
                                className={`col-md-${12 / post.photos.length}`}
                            >
                                <img
                                    src={`data:${
                                        photo.contentType
                                    };base64,${Buffer.from(photo.data).toString(
                                        "base64"
                                    )}`}
                                    className="card-img-top"
                                    alt=""
                                />
                            </div>
                        ))}
                </div>
                <div className="mt-4">
                    <div className="input-group">
                        <label className="input-group-text"> Add comment</label>
                        <input
                            type="text"
                            name="comment"
                            className="form-control"
                            onChange={formHandler}
                            value={comment}
                        />
                        <button
                            type="button"
                            className="btn btn-md btn-primary"
                            onClick={() => {
                                setComment("");
                                submitHandler(post._id, comment);
                            }}
                        >
                            Add
                        </button>
                    </div>
                    <ul className="list-unstyled">
                        {post.comments.map((comment) => (
                            <li key={comment._id}>
                                {comment.data}{" "}
                                {comment.user === userId && (
                                    <button
                                        type="button"
                                        className="btn btn-danger btn-sm m-1 "
                                        onClick={() =>
                                            deleteCommentHandler(
                                                post._id,
                                                comment._id
                                            )
                                        }
                                    >
                                        <FontAwesomeIcon icon={faTrash} />
                                    </button>
                                )}
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        </div>
    );
}
