import React, { useEffect, useState } from "react";
import { userInfo } from "../API/auth";
import { getCategory } from "../API/utilities";
import Search from "./Search";

export default function Filter({ handleFilter, searchedValue }) {
    const [categories, setCategories] = useState([]);

    const [checkCategory, setCheckCategory] = useState([]);
    const [checkReligion, setCheckReligion] = useState([]);
    const [checkTribe, setCheckTribe] = useState([]);
    const [checkBoobs, setCheckBoobs] = useState([]);

    const handleChange = (e) => {
        const newCheckCategory = [...checkCategory];
        const newCheckReligion = [...checkReligion];
        const newCheckTribe = [...checkTribe];
        const newCheckBoobs = [...checkBoobs];
        //console.log(newCheckCategory.indexOf(e.target.value));
        if (e.target.name === "category") {
            if (newCheckCategory.indexOf(e.target.value) === -1) {
                newCheckCategory.push(e.target.value);
            } else {
                newCheckCategory.splice(
                    newCheckCategory.indexOf(e.target.value),
                    1
                );
            }
            setCheckCategory(newCheckCategory);
        }
        if (e.target.name === "religion") {
            if (newCheckReligion.indexOf(e.target.value) === -1) {
                newCheckReligion.push(e.target.value);
            } else {
                newCheckReligion.splice(
                    newCheckReligion.indexOf(e.target.value),
                    1
                );
            }
            setCheckReligion(newCheckReligion);
        }
        if (e.target.name === "tribe") {
            if (newCheckTribe.indexOf(e.target.value) === -1) {
                newCheckTribe.push(e.target.value);
            } else {
                newCheckTribe.splice(newCheckTribe.indexOf(e.target.value), 1);
            }
            setCheckTribe(newCheckTribe);
        }
        if (e.target.name === "boobs") {
            if (newCheckBoobs.indexOf(e.target.value) === -1) {
                newCheckBoobs.push(e.target.value);
            } else {
                newCheckBoobs.splice(newCheckBoobs.indexOf(e.target.value), 1);
            }
            setCheckBoobs(newCheckBoobs);
        }

        handleFilter({
            category: newCheckCategory,
            religion: newCheckReligion,
            tribe: newCheckTribe,
            boobs: newCheckBoobs,
        });
    };

    useEffect(() => {
        getCategory(userInfo().token)
            .then((result) => setCategories(result.data))
            .catch((err) => console.log(err));
    }, []);

    return (
        <div>
            <Search searchedValue={searchedValue} />
            <h5>Filter Entertainer</h5>
            <div className="my-3">
                <h6>By category</h6>
                <ul className="list-group">
                    {categories.map((item) => (
                        <li className="list-group-item" key={item._id}>
                            <input
                                type="checkbox"
                                id={item.name}
                                name="category"
                                value={item._id}
                                onChange={handleChange}
                            />
                            <label for={item.name}>{item.name}</label>
                        </li>
                    ))}
                </ul>
            </div>
            <div className="my-3">
                <h6>By Religion</h6>
                <ul className="list-group">
                    <li className="list-group-item">
                        <input
                            type="checkbox"
                            id="muslim"
                            name="religion"
                            value="muslim"
                            onChange={handleChange}
                        />
                        <label for="muslim">Muslim</label>
                    </li>
                    <li className="list-group-item">
                        <input
                            type="checkbox"
                            id="hindu"
                            name="religion"
                            value="hindu"
                            onChange={handleChange}
                        />
                        <label for="hindu">Hindu</label>
                    </li>
                    <li className="list-group-item">
                        <input
                            type="checkbox"
                            id="christian"
                            name="religion"
                            value="christian"
                            onChange={handleChange}
                        />
                        <label for="christian">Christian</label>
                    </li>
                </ul>
            </div>
            <div className="my-3">
                <h6>By Tribe</h6>
                <ul className="list-group">
                    <li className="list-group-item">
                        <input
                            type="checkbox"
                            id="bangali"
                            name="tribe"
                            value={false}
                            onChange={handleChange}
                        />
                        <label for="bangali">Bangali</label>
                    </li>
                    <li className="list-group-item">
                        <input
                            type="checkbox"
                            id="chakma"
                            name="tribe"
                            value={true}
                            onChange={handleChange}
                        />
                        <label for="chakma">Chakma</label>
                    </li>
                </ul>
            </div>
            <div className="my-3">
                <h6>By Boobs</h6>
                <ul className="list-group">
                    <li className="list-group-item">
                        <input
                            type="checkbox"
                            id="large"
                            name="boobs"
                            value="large"
                            onChange={handleChange}
                        />
                        <label for="large">Large</label>
                    </li>
                    <li className="list-group-item">
                        <input
                            type="checkbox"
                            id="medium"
                            name="boobs"
                            value="medium"
                            onChange={handleChange}
                        />
                        <label for="medium">Medium</label>
                    </li>
                    <li className="list-group-item">
                        <input
                            type="checkbox"
                            id="small"
                            name="boobs"
                            value="small"
                            onChange={handleChange}
                        />
                        <label for="small">Small</label>
                    </li>
                </ul>
            </div>
        </div>
    );
}
