import React, { useEffect, useState } from "react";
import { Buffer } from "buffer";
import Layout from "../Layout";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
import Typography from "@mui/material/Typography";
import Rating from "@mui/material/Rating";
import Modal from "../../Utilities/Modal";
import { useParams, Link } from "react-router-dom";
import {
    getEntertainerById,
    getReviewById,
    updateReview,
    latestOrderByUser,
    updateOrder,
    createCart,
    addPhotosById,
    deletePhotoById,
} from "../../API/utilities";
import { userInfo } from "../../API/auth";

export default function EntertainerDetails(props) {
    const [values, setValues] = useState({
        id: "",
        name: "",
        username: "",
        religion: "",
        tribe: "",
        boobs: "",
        category: "",
        availability: "",
        profile: {},
        photos: [],
        newPhoto: "",
    });

    const [message, setMessage] = useState("");
    const [latestOrder, setLatestOrder] = useState({});
    const [review, setReview] = useState(0);

    const id = useParams().id;

    const cartHandler = () => {
        const cart = {
            product: values.id,
            user: userInfo().decoded.id,
            refModel: "entertainer",
        };
        createCart(cart, userInfo().token)
            .then((result) => setMessage("successfully added to cart!"))
            .catch((err) => setMessage("failed to added to cart!"));
    };

    const formHandler = (e) => {
        setValues({
            ...values,
            newPhoto: e.target.files[0],
        });
    };

    const reviewHandler = (e) => {
        updateReview({ id: id, review: e.target.value }, userInfo().token)
            .then(() => {
                updateOrder(
                    latestOrder._id,
                    { reviewStatus: true },
                    userInfo().token
                )
                    .then((result) => {
                        latestOrderByUser(id, userInfo().token)
                            .then((result) => setLatestOrder(result.data))
                            .catch((err) => console.log(err));
                    })
                    .catch((err) => console.log(err));
                getReviewById(id, userInfo().token)
                    .then((result) => setReview(result.data.review))
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    const submitHandler = (e) => {
        e.preventDefault();
        const formData = new FormData(e.target);
        addPhotosById(id, formData, userInfo().token)
            .then((result) => {
                getEntertainerById(id, userInfo().token)
                    .then((result) => {
                        setValues({
                            ...values,
                            id: result.data._id,
                            name: result.data.name,
                            username: result.data.username,
                            religion: result.data.religion,
                            tribe: result.data.tribe,
                            boobs: result.data.boobs,
                            category: result.data.category.name,
                            availability: result.data.availability,
                            profile: result.data.profile,
                            photos: result.data.photos,
                        });
                    })
                    .catch((err) => console.log(err));
            })
            .catch((err) => setMessage("something went wrong!"));
    };

    const deleteHandler = (entertainerId, photoId) => {
        deletePhotoById(
            { entertainerId: entertainerId, photoId: photoId },
            userInfo().token
        )
            .then(() => {
                getEntertainerById(id, userInfo().token)
                    .then((result) => {
                        setValues({
                            ...values,
                            id: result.data._id,
                            name: result.data.name,
                            username: result.data.username,
                            religion: result.data.religion,
                            tribe: result.data.tribe,
                            boobs: result.data.boobs,
                            category: result.data.category.name,
                            availability: result.data.availability,
                            profile: result.data.profile,
                            photos: result.data.photos,
                        });
                    })
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    useEffect(() => {
        getEntertainerById(id, userInfo().token)
            .then((result) => {
                setValues({
                    ...values,
                    id: result.data._id,
                    name: result.data.name,
                    username: result.data.username,
                    religion: result.data.religion,
                    tribe: result.data.tribe,
                    boobs: result.data.boobs,
                    category: result.data.category.name,
                    availability: result.data.availability,
                    profile: result.data.profile,
                    photos: result.data.photos,
                });
            })
            .catch((err) => console.log(err));

        getReviewById(id, userInfo().token)
            .then((result) => setReview(result.data.review))
            .catch((err) => console.log(err));

        latestOrderByUser(id, userInfo().token)
            .then((result) => setLatestOrder(result.data))
            .catch((err) => console.log(err));
    }, []);

    return (
        <Layout title={values.name}>
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {message && (
                            <div className="alert alert-success" role="alert">
                                {message}
                            </div>
                        )}
                        <div className="row">
                            <div className="col-md-4">
                                <div className="card">
                                    {values.profile.data && (
                                        <>
                                            <Link
                                                data-bs-toggle="modal"
                                                data-bs-target="#imageModal"
                                            >
                                                <img
                                                    src={`data:${
                                                        values.profile
                                                            .contentType
                                                    };base64,${Buffer.from(
                                                        values.profile.data
                                                    ).toString("base64")}`}
                                                    className="card-img-top"
                                                    alt={values.name}
                                                />
                                            </Link>
                                            <Modal
                                                name={values.name}
                                                src={`data:${
                                                    values.profile.contentType
                                                };base64,${Buffer.from(
                                                    values.profile.data
                                                ).toString("base64")}`}
                                            />
                                        </>
                                    )}
                                    <div className="card-body lead">
                                        <h5 className="card-title">
                                            {values.name}
                                        </h5>
                                        <p className="card-text">
                                            username: {values.username} <br />
                                            Religion: {values.religion} <br />
                                            Tribe:{" "}
                                            {values.tribe === true
                                                ? "Chakma"
                                                : "Bangali"}{" "}
                                            <br />
                                            Boobs Size: {values.boobs} <br />
                                            {values.category} service available.{" "}
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-8 my-3">
                                <div className="row">
                                    {values.photos.map((photo) => (
                                        <div
                                            key={photo._id}
                                            className={`col-md-${
                                                12 / values.photos.length
                                            }`}
                                        >
                                            <img
                                                src={`data:${
                                                    photo.contentType
                                                };base64,${Buffer.from(
                                                    photo.data
                                                ).toString("base64")}`}
                                                className="img-fluid"
                                                alt={values.name}
                                            />
                                            {userInfo().decoded.role ===
                                                "admin" && (
                                                <button
                                                    type="button"
                                                    className="btn btn-danger btn-sm m-1"
                                                    onClick={() =>
                                                        deleteHandler(
                                                            id,
                                                            photo._id
                                                        )
                                                    }
                                                >
                                                    <FontAwesomeIcon
                                                        icon={faTrash}
                                                    />
                                                </button>
                                            )}
                                        </div>
                                    ))}
                                    <div>
                                        <div>
                                            <Typography component="legend">
                                                {values.name}'s Rating
                                            </Typography>
                                            <Rating
                                                name="read-only"
                                                value={review}
                                                readOnly
                                            />
                                        </div>
                                        {userInfo().decoded.role ===
                                            "admin" && (
                                            <form
                                                className="my-2"
                                                onSubmit={submitHandler}
                                            >
                                                <div className="form-group mb-1">
                                                    <label for="photo">
                                                        Add photos
                                                    </label>
                                                    <input
                                                        type="file"
                                                        className="form-control-file"
                                                        id="photo"
                                                        name="photos"
                                                        multiple="multiple"
                                                        onChange={formHandler}
                                                    />
                                                </div>
                                                {values.newPhoto && (
                                                    <button
                                                        type="submit"
                                                        className="btn btn-primary"
                                                    >
                                                        Submit
                                                    </button>
                                                )}
                                            </form>
                                        )}
                                        {latestOrder.done === true &&
                                            latestOrder.reviewStatus ===
                                                false && (
                                                <div>
                                                    <Typography component="legend">
                                                        Add your rating for{" "}
                                                        {values.name}
                                                    </Typography>
                                                    <Rating
                                                        name="simple-controlled"
                                                        onChange={reviewHandler}
                                                    />
                                                </div>
                                            )}
                                        <div className="my-2">
                                            <span className="badge bg-primary">
                                                {values.availability === true
                                                    ? "Available Now"
                                                    : "Not Available"}
                                            </span>
                                        </div>
                                        {values.availability === true &&
                                            userInfo().decoded.role !==
                                                "admin" &&
                                            latestOrder.reviewStatus ===
                                                true && (
                                                <button
                                                    onClick={cartHandler}
                                                    className="btn btn-sm btn-primary"
                                                >
                                                    Add to Cart
                                                </button>
                                            )}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
