import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Layout from "../Layout";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
import { getOrder, deleteOrder, updateOrder } from "../../API/utilities";
import { userInfo } from "../../API/auth";

export default function Orders(props) {
    const [orders, setOrders] = useState([]);
    const [message, setMessage] = useState(false);

    const deleteHandler = (id) => {
        deleteOrder(id, userInfo().token)
            .then((result) => {
                setMessage("order deleted successfully!");
                getOrder(userInfo().token)
                    .then((result) => setOrders(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => setMessage("product delete failed!"));
    };

    const orderHandler = (order) => {
        const data = { done: order.done === false ? true : false };
        updateOrder(order._id, data, userInfo().token).then((result) => {
            getOrder(userInfo().token)
                .then((result) => setOrders(result.data))
                .catch((err) => console.log(err));
        });
    };

    useEffect(() => {
        getOrder(userInfo().token)
            .then((result) => setOrders(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Order">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Orders</h1>
                    {message && (
                        <div className="alert alert-success" role="alert">
                            {message}
                        </div>
                    )}
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        <div className="row">
                            <div className="col-12">
                                {orders.map((item) => (
                                    <div className="card my-1" key={item._id}>
                                        <div className="card-body">
                                            <p>
                                                <span className="fw-bold">
                                                    Order ID:
                                                </span>{" "}
                                                {item._id}
                                            </p>
                                            <p>
                                                <span className="fw-bold">
                                                    Customer Email:
                                                </span>{" "}
                                                {item.user.email}
                                            </p>
                                            <p>
                                                <span className="fw-bold">
                                                    Entertainer Name:
                                                </span>{" "}
                                                <Link
                                                    className="text-decoration-none text-dark fw-bold"
                                                    to={`/${item.refModel}/${item.product._id}`}
                                                >
                                                    {item.product.name}
                                                </Link>
                                            </p>
                                            <p>
                                                <span className="fw-bold">
                                                    Order Time:
                                                </span>{" "}
                                                {item.time}
                                            </p>
                                            <p>
                                                <span className="fw-bold">
                                                    Order Status:
                                                </span>{" "}
                                                <button
                                                    className="btn btn-secondary"
                                                    onClick={() =>
                                                        orderHandler(item)
                                                    }
                                                >
                                                    {item.done === false
                                                        ? "Pending"
                                                        : "Done"}
                                                </button>
                                            </p>
                                            <span>
                                                <button
                                                    type="button"
                                                    className="btn btn-danger btn-sm m-1"
                                                    onClick={() =>
                                                        deleteHandler(item._id)
                                                    }
                                                >
                                                    <FontAwesomeIcon
                                                        icon={faTrash}
                                                    />
                                                </button>
                                            </span>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
