import React from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import { userInfo } from "../../API/auth";
import { isDayTime } from "../../Utilities/utilities";
import "./Sidebar.css";

export default function Sidebar() {
    return (
        <div className="border-right" id="sidebar-wrapper">
            <div className="sidebar-heading d-flex justify-content-between">
                <h5 className="mx-2">
                    <Link className="navbar-brand" to="/dashboard">
                        {userInfo().decoded.name}
                    </Link>
                </h5>
                <button
                    className="btn btn-dark btn-sm mx-2"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#sidebarNav"
                >
                    <FontAwesomeIcon icon={faBars} />
                </button>
            </div>
            <div className="list-group list-group-flush" id="sidebarNav">
                {userInfo().decoded.role === "admin" ? (
                    <>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/orders"
                        >
                            Orders
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/entertainerlist"
                        >
                            Entertainer List
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/category"
                        >
                            Category
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/post"
                        >
                            My Post
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/posts"
                        >
                            Posts
                        </Link>
                    </>
                ) : (
                    <>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/getyourentertainer"
                        >
                            Get Your Entertainer {isDayTime()}
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/entertainer"
                        >
                            Entertainers
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/post"
                        >
                            My Post
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/posts"
                        >
                            Posts
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/cart"
                        >
                            Cart
                        </Link>
                        <Link
                            className="list-group-item list-group-item-action"
                            to="/order"
                        >
                            Your Order
                        </Link>
                    </>
                )}
            </div>
        </div>
    );
}
