import React, { useState } from "react";
import { Navigate } from "react-router-dom";
import { signIn, signUp, auth } from "../../API/auth";
import Layout from "../Layout";

function Login(props) {
    const [values, setValues] = useState({
        mode: "signIn",
        message: "",
        name: "",
        email: "",
        password: "",
        success: false,
    });
    const { mode, message, name, email, password, success } = values;

    const modeHandler = () => {
        setValues({
            ...values,
            mode: mode === "signIn" ? "signUp" : "signIn",
        });
    };

    const formHandler = (e) => {
        setValues({
            ...values,
            [e.target.name]: e.target.value,
        });
    };

    const submitHandler = (e) => {
        e.preventDefault();
        if (mode === "signIn") {
            signIn({ email, password })
                .then((result) => {
                    setValues({
                        ...values,
                        success: true,
                        email: "",
                        password: "",
                    });
                    auth(result.data.token);
                })
                .catch((err) => {
                    setValues({
                        ...values,
                        message: "invalid email or password!",
                    });
                });
        } else {
            signUp({
                name,
                email,
                password,
            })
                .then((result) => {
                    setValues({
                        ...this.state,
                        success: true,
                        name: "",
                        email: "",
                        password: "",
                    });
                    auth(result.data.token);
                })
                .catch((err) => {
                    setValues({
                        ...values,
                        message: "invalid email or password!",
                    });
                });
        }
    };

    return (
        <Layout title="Login">
            {success && <Navigate replace to="/dashboard" />}
            <div className="row justify-content-center">
                <div className="col-md-6">
                    <div className="card mt-5">
                        <div className="card-header">
                            <h4>{mode === "signIn" ? "Sign in" : "Sign up"}</h4>
                        </div>
                        <div className="card-body">
                            {message && (
                                <div
                                    className="alert alert-danger"
                                    role="alert"
                                >
                                    {message}
                                </div>
                            )}
                            <form onSubmit={submitHandler}>
                                {mode === "signUp" ? (
                                    <>
                                        <div className="form-group">
                                            <label for="name" className="mb-1">
                                                Name
                                            </label>
                                            <input
                                                type="text"
                                                className="form-control mb-1"
                                                id="name"
                                                name="name"
                                                value={name}
                                                onChange={formHandler}
                                                placeholder="Enter Your Name"
                                            />
                                        </div>
                                        <div className="form-group">
                                            <label for="email" className="mb-1">
                                                Email
                                            </label>
                                            <input
                                                type="email"
                                                className="form-control mb-1"
                                                id="email"
                                                name="email"
                                                value={email}
                                                onChange={formHandler}
                                                placeholder="Enter Your Email"
                                            />
                                        </div>
                                        <div className="form-group">
                                            <label
                                                for="password"
                                                className="mb-1"
                                            >
                                                Password
                                            </label>
                                            <input
                                                type="password"
                                                className="form-control mb-1"
                                                id="password"
                                                name="password"
                                                value={password}
                                                onChange={formHandler}
                                                placeholder="Password"
                                            />
                                        </div>
                                    </>
                                ) : (
                                    <>
                                        <div className="form-group">
                                            <label for="email" className="mb-1">
                                                Email
                                            </label>
                                            <input
                                                type="email"
                                                className="form-control mb-1"
                                                id="email"
                                                name="email"
                                                value={email}
                                                onChange={formHandler}
                                                placeholder="Enter Your Email"
                                            />
                                        </div>
                                        <div className="form-group">
                                            <label
                                                for="password"
                                                className="mb-1"
                                            >
                                                Password
                                            </label>
                                            <input
                                                type="password"
                                                className="form-control mb-1"
                                                id="password"
                                                name="password"
                                                value={password}
                                                onChange={formHandler}
                                                placeholder="Password"
                                            />
                                        </div>
                                    </>
                                )}

                                <div className="text-end">
                                    <button
                                        type="submit"
                                        className="btn btn-primary"
                                    >
                                        {mode === "signIn"
                                            ? "Sign in"
                                            : "Sign Up"}
                                    </button>
                                </div>
                            </form>
                        </div>
                        <div className="card-footer">
                            <p className="text-center" onClick={modeHandler}>
                                {mode === "signIn"
                                    ? "Not registered?"
                                    : "Already have an account?"}{" "}
                                <span className="text-warning">
                                    {mode === "signIn"
                                        ? "Create an account"
                                        : "Sign in"}
                                </span>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}

export default Login;
