import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import { userInfo } from "../../API/auth";
import { getCategory, createEntertainer } from "../../API/utilities";

export default function CreateEntertainer(props) {
    const [values, setValues] = useState({
        message: "",
        name: "",
        username: "",
        religion: "",
        tribe: "",
        boobs: "",
        category: "",
        formData: new FormData(),
    });
    const [categories, setCategories] = useState([]);

    const {
        message,
        name,
        username,
        religion,
        tribe,
        boobs,
        category,
        formData,
    } = values;

    const formHandler = (e) => {
        const value =
            e.target.name === "photos" || e.target.name === "profile"
                ? e.target.files
                : e.target.value;
        if (e.target.name === "photos" || e.target.name === "profile") {
            for (let i = 0; i < value.length; i++) {
                formData.append(e.target.name, value[i]);
            }
        } else {
            formData.set(e.target.name, value);
        }
        setValues({
            ...values,
            [e.target.name]: e.target.value,
        });
    };

    const submitHandler = (e) => {
        e.preventDefault();
        createEntertainer(formData, userInfo().token)
            .then((result) => {
                setValues({
                    ...values,
                    message: "Entertainer successfully added!",
                    name: "",
                    username: "",
                    religion: "",
                    tribe: "",
                    category: "",
                });
            })
            .catch((err) => {
                console.log(err);
                setValues({ ...values, message: "something weng wrong!" });
            });
    };

    useEffect(() => {
        getCategory(userInfo().token)
            .then((result) => setCategories(result.data))
            .catch((err) => console.log(err));
    }, []);

    return (
        <Layout title="Create New Entertainer">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Create Entertainer</h1>
                    <div
                        style={{ paddingTop: "5rem", paddingBottom: "5rem" }}
                        className="container"
                    >
                        {message && (
                            <div className="alert alert-success" role="alert">
                                {message}
                            </div>
                        )}
                        <form
                            onSubmit={submitHandler}
                            encType="multipart/form-data"
                        >
                            <div className="form-group mb-1">
                                <label for="name">Entertainer Name</label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="name"
                                    name="name"
                                    value={name}
                                    onChange={formHandler}
                                    placeholder="Enter Name"
                                />
                            </div>
                            <div className="form-group mb-1">
                                <label for="username">
                                    Entertainer Username
                                </label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="username"
                                    name="username"
                                    value={username}
                                    onChange={formHandler}
                                    placeholder="Enter Username"
                                />
                            </div>
                            <div className="form-group mb-1">
                                <label for="religion">Religion</label>
                                <select
                                    className="form-control"
                                    id="religion"
                                    name="religion"
                                    value={religion}
                                    onChange={formHandler}
                                >
                                    <option>select religion</option>
                                    <option>muslim</option>
                                    <option>hindu</option>
                                    <option>christian</option>
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="tribe">Tribe</label>
                                <select
                                    className="form-control"
                                    id="tribe"
                                    name="tribe"
                                    value={tribe}
                                    onChange={formHandler}
                                >
                                    <option>select tribe</option>
                                    <option value={0}>Bangali</option>
                                    <option value={1}>Chakma</option>
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="boobs">Boobs Size</label>
                                <select
                                    className="form-control"
                                    id="boobs"
                                    name="boobs"
                                    value={boobs}
                                    onChange={formHandler}
                                >
                                    <option>select boobs size</option>
                                    <option>large</option>
                                    <option>medium</option>
                                    <option>small</option>
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="category">Category</label>
                                <select
                                    className="form-control"
                                    id="category"
                                    name="category"
                                    value={category}
                                    onChange={formHandler}
                                >
                                    <option>select product category</option>
                                    {categories.map((item) => (
                                        <option key={item._id} value={item._id}>
                                            {item.name}
                                        </option>
                                    ))}
                                </select>
                            </div>
                            <div className="form-group mb-1">
                                <label for="profile">Profile Photo</label>
                                <input
                                    type="file"
                                    className="form-control-file"
                                    id="profile"
                                    name="profile"
                                    onChange={formHandler}
                                />
                            </div>
                            <div className="form-group mb-1">
                                <label for="photo">Photos</label>
                                <input
                                    type="file"
                                    className="form-control-file"
                                    id="photo"
                                    name="photos"
                                    multiple="multiple"
                                    onChange={formHandler}
                                />
                            </div>
                            <button type="submit" className="btn btn-primary">
                                Submit
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
