import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import PostCard from "./PostCard";
import { userInfo } from "../../API/auth";
import {
    getPostByUserId,
    deletePost,
    updatePost,
    deleteComment,
} from "../../API/utilities";

export default function Post(props) {
    const [posts, setPosts] = useState([]);

    const submitHandler = (id, data) => {
        const formData = new FormData();
        formData.set("comments", data);
        updatePost(id, formData, userInfo().token)
            .then((result) => {
                getPostByUserId(userInfo().token)
                    .then((result) => setPosts(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    const deleteCommentHandler = (postId, commentId) => {
        deleteComment(
            { postId: postId, commentId: commentId },
            userInfo().token
        )
            .then((result) => {
                getPostByUserId(userInfo().token)
                    .then((result) => setPosts(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    const deleteHandler = (id) => {
        deletePost(id, userInfo().token)
            .then((result) => {
                getPostByUserId(userInfo().token)
                    .then((result) => setPosts(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    useEffect(() => {
        getPostByUserId(userInfo().token)
            .then((result) => setPosts(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Post">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        <div className="row justify-content-center">
                            {posts.map((post) => (
                                <PostCard
                                    key={post._id}
                                    post={post}
                                    userId={userInfo().decoded.id}
                                    submitHandler={submitHandler}
                                    deleteCommentHandler={deleteCommentHandler}
                                    deleteHandler={deleteHandler}
                                />
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
