import React, { Component } from "react";
import { Navigate } from "react-router-dom";
import Layout from "../Layout";
import "./Home.css";
import { auth, signIn, signUp } from "../../API/auth";

export default class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            mode: "signIn",
            success: false,
            name: "",
            email: "",
            password: "",
        };
    }

    modeHandler = () => {
        this.setState({
            ...this.state,
            mode: this.state.mode === "signIn" ? "signUp" : "signIn",
        });
    };

    handleForm = (e) => {
        this.setState({
            ...this.state,
            [e.target.name]: e.target.value,
        });
    };

    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.mode === "signIn") {
            signIn({ email: this.state.email, password: this.state.password })
                .then((result) => {
                    this.setState({
                        ...this.state,
                        success: true,
                        email: "",
                        password: "",
                    });
                    auth(result.data.token);
                })
                .catch((err) => {
                    this.setState({
                        ...this.state,
                        message: "invalid email or password!",
                    });
                });
        } else {
            signUp({
                name: this.state.name,
                email: this.state.email,
                password: this.state.password,
            })
                .then((result) => {
                    this.setState({
                        ...this.state,
                        success: true,
                        name: "",
                        email: "",
                        password: "",
                    });
                    auth(result.data.token);
                })
                .catch((err) => {
                    this.setState({
                        ...this.state,
                        message: "invalid email or password!",
                    });
                });
        }
    };

    render() {
        return (
            <Layout title="Home">
                {this.state.success && <Navigate replace to="/dashboard" />}
                <div className="row justify-content-center cover-image text-light">
                    <div className="col-md-6">
                        <div className="card border border-light mt-5">
                            <div className="card-header">
                                <h4>
                                    {this.state.mode === "signIn"
                                        ? "Sign in"
                                        : "Sign up"}
                                </h4>
                            </div>
                            <div className="card-body">
                                {this.state.message && (
                                    <div
                                        className="alert alert-danger"
                                        role="alert"
                                    >
                                        {this.state.message}
                                    </div>
                                )}
                                <form onSubmit={this.handleSubmit}>
                                    {this.state.mode === "signUp" ? (
                                        <>
                                            <div className="form-group">
                                                <label
                                                    for="name"
                                                    className="mb-1"
                                                >
                                                    Name
                                                </label>
                                                <input
                                                    type="text"
                                                    className="form-control mb-1"
                                                    id="name"
                                                    name="name"
                                                    placeholder="Enter Your Name"
                                                    value={this.state.name}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label
                                                    for="email"
                                                    className="mb-1"
                                                >
                                                    Email
                                                </label>
                                                <input
                                                    type="text"
                                                    className="form-control mb-1"
                                                    id="email"
                                                    name="email"
                                                    placeholder="Enter Your Email"
                                                    value={this.state.email}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label
                                                    for="password"
                                                    className="mb-1"
                                                >
                                                    Password
                                                </label>
                                                <input
                                                    type="password"
                                                    className="form-control mb-1"
                                                    id="password"
                                                    name="password"
                                                    placeholder="Password"
                                                    value={this.state.password}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                        </>
                                    ) : (
                                        <>
                                            <div className="form-group">
                                                <label
                                                    for="email"
                                                    className="mb-1"
                                                >
                                                    Email
                                                </label>
                                                <input
                                                    type="text"
                                                    className="form-control mb-1"
                                                    id="email"
                                                    name="email"
                                                    placeholder="Enter Your Email"
                                                    value={this.state.email}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <label
                                                    for="password"
                                                    className="mb-1"
                                                >
                                                    Password
                                                </label>
                                                <input
                                                    type="password"
                                                    className="form-control mb-1"
                                                    id="password"
                                                    name="password"
                                                    placeholder="Password"
                                                    value={this.state.password}
                                                    onChange={this.handleForm}
                                                />
                                            </div>
                                        </>
                                    )}

                                    <div className="text-end">
                                        <button
                                            type="submit"
                                            className="btn btn-primary"
                                        >
                                            {this.state.mode === "signIn"
                                                ? "Sign in"
                                                : "Sign Up"}
                                        </button>
                                    </div>
                                </form>
                            </div>
                            <div className="card-footer">
                                <p
                                    className="text-center"
                                    onClick={this.modeHandler}
                                >
                                    {this.state.mode === "signIn"
                                        ? "Not registered?"
                                        : "Already have an account?"}{" "}
                                    <span className="text-warning">
                                        {this.state.mode === "signIn"
                                            ? "Create an account"
                                            : "Sign in"}
                                    </span>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}
