import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import { URL } from "../../API/url";
import { userInfo } from "../../API/auth";
import { getEntertainer } from "../../API/utilities";
import { Link } from "react-router-dom";

export default function GetYourEntertainer(props) {
    const [entertainer, setEntertainer] = useState({
        _id: "",
        name: "",
        username: "",
        religion: "",
        tribe: "",
        category: "",
    });

    useEffect(() => {
        getEntertainer(userInfo().token)
            .then((result) => {
                const code = Math.floor(Math.random() * result.data.length);
                setEntertainer({
                    ...entertainer,
                    _id: result.data[code]._id,
                    name: result.data[code].name,
                    username: result.data[code].username,
                    religion: result.data[code].religion,
                    tribe: result.data[code].tribe,
                    category: result.data[code].category.name,
                });
            })
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Get Your Entertainer">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        <div className="row">
                            <div className="col">
                                <div className="card m-5">
                                    <Link
                                        to={`/entertainer/${entertainer._id}`}
                                        className="nav-link"
                                    >
                                        <div className="row no-gutters">
                                            <div className="col-md-4">
                                                {entertainer._id && (
                                                    <img
                                                        src={`${URL}/entertainer/getProfile/${entertainer._id}`}
                                                        className="card-img"
                                                        alt=""
                                                    />
                                                )}
                                            </div>
                                            <div className="col-md-8">
                                                <div className="card-body lead">
                                                    <h1 className="card-title">
                                                        {entertainer.name}
                                                    </h1>
                                                    <p className="card-text">
                                                        {entertainer.username}
                                                    </p>
                                                    <p className="card-text">
                                                        {entertainer.tribe ===
                                                        false
                                                            ? "Bangali"
                                                            : "Chakma"}
                                                    </p>
                                                    <p className="card-text">
                                                        {entertainer.category}{" "}
                                                        service available
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
