import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    faPlus,
    faVenus,
    faChartSimple,
} from "@fortawesome/free-solid-svg-icons";
import {
    newArrival,
    recentOrder,
    getInteractionById,
} from "../../API/utilities";
import { URL } from "../../API/url";
import { isDayTime } from "../../Utilities/utilities";
import { userInfo } from "../../API/auth";

function Dashboard(props) {
    const [newCollection, setNewCollection] = useState([]);
    const [recentOrders, setRecentOrders] = useState([]);
    const [interaction, setInteraction] = useState({});

    const decoded = userInfo().decoded;

    let interactionCount = 0;
    if (interaction.productInteraction) {
        interactionCount = interaction.productInteraction.reduce(
            (total, product) => product.orderCount + total,
            0
        );
    }

    useEffect(() => {
        newArrival(userInfo().token)
            .then((result) => setNewCollection(result.data))
            .catch((error) => console.log(error));
        recentOrder(userInfo().token)
            .then((result) => setRecentOrders(result.data))
            .catch((error) => console.log(error));
        getInteractionById(userInfo().token)
            .then((result) => setInteraction(result.data))
            .catch((error) => console.log(error));
    }, []);
    return (
        <Layout title="Dashboard">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        <div className="row mb-3">
                            <div className="col">
                                <h1 className="display-4">
                                    Welcome to Dark World Fantasy.
                                </h1>
                                <p className="lead">
                                    Now this world is yours to enjoy. Here you
                                    can have what you want. You can have from
                                    teenger girl to 35 years old milf. We have
                                    collections from school, college and varsity
                                    student to housewife. Now you can choose
                                    your own entertainer. Have fun...
                                </p>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <div className="col-10 card">
                                <div className="card-body d-flex flex-row p-3">
                                    <div className="p-3">
                                        <FontAwesomeIcon
                                            style={{ fontSize: "15rem" }}
                                            icon={faChartSimple}
                                        />
                                    </div>
                                    <div className="p-3">
                                        <h4>Analysis</h4>
                                        <p>
                                            Total Enjoyed: {interactionCount}{" "}
                                            times
                                        </p>
                                        <table className="table">
                                            <thead>
                                                <tr>
                                                    <th scope="col">#</th>
                                                    <th scope="col">Name</th>
                                                    <th scope="col">Enjoyed</th>
                                                    <th scope="col">
                                                        Percentage (%)
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <th scope="row">1</th>
                                                    <td>
                                                        Atifa Hossain Prottasha
                                                    </td>
                                                    <td>Otto</td>
                                                    <td>@mdo</td>
                                                </tr>
                                                <tr>
                                                    <th scope="row">2</th>
                                                    <td>Parisha Tabassum</td>
                                                    <td>Thornton</td>
                                                    <td>@fat</td>
                                                </tr>
                                                <tr>
                                                    <th scope="row">3</th>
                                                    <td colspan="2">
                                                        Larry the Bird
                                                    </td>
                                                    <td>@twitter</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                        <div className="text-end">
                                            <Link
                                                className="text-primary text-decoration-none"
                                                to="/analysis"
                                            >
                                                See All
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row mb-3">
                            {recentOrders.length > 0 && (
                                <div className="col-4 card me-3">
                                    <div className="card-body">
                                        <h4>Recent</h4>
                                        <ul className="list-unorderd">
                                            {recentOrders.map((orders) => (
                                                <li key={orders._id}>
                                                    <Link
                                                        className="text-decoration-none fw-bold text-dark"
                                                        to={`/entertainer/${orders.product._id}`}
                                                    >
                                                        {orders.product.name}
                                                    </Link>
                                                </li>
                                            ))}
                                        </ul>
                                    </div>
                                </div>
                            )}
                            <div className="col-4 card ml-3">
                                <div className="card-body">
                                    <h4>Top Rated Entertainers</h4>
                                    <ul className="list-unorderd">
                                        <li>Alifa Hossain Prottasha</li>
                                        <li>Parisha Tabassum</li>
                                        <li>Saima Orin</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="row mb-3">
                            <div className="col card me-3">
                                <div className="card-body">
                                    <h4>New Arrival</h4>
                                    <div className="row">
                                        {newCollection.map((entertainer) => (
                                            <div
                                                key={entertainer._id}
                                                className="col"
                                            >
                                                <div
                                                    style={{
                                                        background: "#D8D8D8",
                                                    }}
                                                    className="card"
                                                >
                                                    <Link
                                                        to={`/entertainer/${entertainer._id}`}
                                                        className="nav-link"
                                                    >
                                                        <div className="card-body d-flex flex-row p-0">
                                                            <div
                                                                style={{
                                                                    width: "150px",
                                                                    height: "150px",
                                                                    overflow:
                                                                        "hidden",
                                                                }}
                                                            >
                                                                <img
                                                                    className="img-fluid"
                                                                    src={`${URL}/entertainer/getProfile/${entertainer._id}`}
                                                                    alt=""
                                                                />
                                                            </div>
                                                            <div className="p-3">
                                                                <p>
                                                                    {
                                                                        entertainer.name
                                                                    }
                                                                </p>
                                                                <p>
                                                                    {
                                                                        entertainer.username
                                                                    }
                                                                </p>
                                                            </div>
                                                        </div>
                                                    </Link>
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            {props.role === "admin" && (
                                <>
                                    <div
                                        style={{
                                            width: "16rem",
                                            display: "inline-block",
                                        }}
                                        className="card m-3"
                                    >
                                        <Link
                                            className="nav-link"
                                            to="/addentertainer"
                                        >
                                            <div className="d-flex p-3">
                                                <FontAwesomeIcon
                                                    className="m-2"
                                                    icon={faPlus}
                                                />
                                                <h4>Add Entertainer</h4>
                                            </div>
                                        </Link>
                                    </div>
                                </>
                            )}
                            <div
                                style={{
                                    width: "16rem",
                                    display: "inline-block",
                                }}
                                className="card m-3"
                            >
                                <Link className="nav-link" to="/createpost">
                                    <div className="d-flex p-3">
                                        <FontAwesomeIcon
                                            className="m-2"
                                            icon={faPlus}
                                        />
                                        <h4>Add Post</h4>
                                    </div>
                                </Link>
                            </div>
                            {props.role !== "admin" && (
                                <div
                                    style={{
                                        width: "16rem",
                                        display: "inline-block",
                                    }}
                                    className="card m-3"
                                >
                                    <Link
                                        className="nav-link"
                                        to="/getyourentertainer"
                                    >
                                        <div className="d-flex p-3">
                                            <FontAwesomeIcon
                                                className="fa-2x m-2"
                                                icon={faVenus}
                                            />
                                            <h4>
                                                Get Your Entertainer{" "}
                                                {isDayTime()}
                                            </h4>
                                        </div>
                                    </Link>
                                </div>
                            )}
                        </div>
                        <div className="row my-5">
                            <ul className="list-group">
                                <li className="list-group-item active">
                                    Your Details
                                </li>
                                <li className="list-group-item text-capitalize">
                                    Name: {decoded.name}
                                </li>
                                <li className="list-group-item">
                                    Email: {decoded.email}
                                </li>
                                <li className="list-group-item text-capitalize">
                                    Role: {decoded.role}
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}

export default Dashboard;
