export const isDayTime = () => {
    // Get current time
    var currentTime = new Date();

    // Get sunrise and sunset times for your location (replace with actual values)
    var sunriseTime = new Date();
    sunriseTime.setHours(6);
    sunriseTime.setMinutes(0);

    var sunsetTime = new Date();
    sunsetTime.setHours(18);
    sunsetTime.setMinutes(0);

    // Compare current time with sunrise and sunset times
    if (currentTime >= sunriseTime && currentTime < sunsetTime) {
        return "Today"; // It is daytime
    } else {
        return "Tonight"; // It is nighttime
    }
};
