import React, { useEffect, useState } from "react";
import * as JsSearch from "js-search";
import { userInfo } from "../API/auth";
import { getEntertainer } from "../API/utilities";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";

export default function Search({ searchedValue }) {
    const [entertainers, setEntertainers] = useState([]);
    const [value, setValue] = useState("");

    let search = new JsSearch.Search("_id");
    search.addIndex("name");
    search.addIndex("username");
    search.addIndex("religion");
    search.addIndex("tribe");
    search.addIndex("boobs");
    search.addIndex(["category", "name"]);

    search.addDocuments(entertainers);

    const handleForm = (e) => {
        setValue(e.target.value);
    };

    const handleSearch = (e) => {
        e.preventDefault();
        searchedValue(search.search(value));
    };

    useEffect(() => {
        getEntertainer(userInfo().token)
            .then((result) => setEntertainers(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <form onSubmit={handleSearch} className="d-flex my-3" role="search">
            <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
                onChange={handleForm}
            />
            <button className="btn btn-outline-success" type="submit">
                <FontAwesomeIcon icon={faMagnifyingGlass} />
            </button>
        </form>
    );
}
