import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faPen } from "@fortawesome/free-solid-svg-icons";
import { userInfo } from "../../API/auth";
import {
    getCategory,
    createCategory,
    deleteCategory,
} from "../../API/utilities";

export default function Category(props) {
    const [value, setValue] = useState("");
    const [category, setCategory] = useState([]);
    const [message, setMessage] = useState(null);

    const formHandler = (e) => {
        setValue(e.target.value);
    };

    const submitHandler = (e) => {
        e.preventDefault();
        createCategory({ name: value }, userInfo().token)
            .then((result) => {
                setValue("");
                getCategory(userInfo().token)
                    .then((result) => setCategory(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => {
                setMessage(err.response.data);
            });
    };

    const deleteHandler = (id) => {
        deleteCategory(id, userInfo().token)
            .then((result) => {
                setMessage("successfully deleted!");
                getCategory(userInfo().token)
                    .then((result) => setCategory(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => console.log(err));
    };

    useEffect(() => {
        getCategory(userInfo().token)
            .then((result) => setCategory(result.data))
            .catch((err) => console.log(err));
    }, []);

    return (
        <Layout title="Category">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Category</h1>
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {message && (
                            <div className="alert alert-success" role="alert">
                                {message}
                            </div>
                        )}
                        <form onSubmit={submitHandler}>
                            <div className="row">
                                <div className="col">
                                    <div className="d-flex">
                                        <FontAwesomeIcon
                                            className="m-3"
                                            icon={faPen}
                                        />
                                        <input
                                            type="text"
                                            className="form-control"
                                            value={value}
                                            onChange={formHandler}
                                        />
                                    </div>
                                </div>
                                <div className="col">
                                    <button
                                        type="submit"
                                        className="btn btn-md btn-primary"
                                    >
                                        Submit
                                    </button>
                                </div>
                            </div>
                        </form>
                        <ul className="list-group">
                            {category.map((item) => (
                                <li
                                    key={item._id}
                                    className="list-group-item d-flex justify-content-between align-items-center my-1 text-capitalize"
                                >
                                    {item.name}
                                    <button
                                        type="button"
                                        className="btn btn-danger btn-sm"
                                        onClick={() => deleteHandler(item._id)}
                                    >
                                        <FontAwesomeIcon icon={faTrash} />
                                    </button>
                                </li>
                            ))}
                        </ul>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
