import React from "react";
import { Routes, Route } from "react-router-dom";
import Home from "./Home/Home";
import Login from "./Login/Login";
import Logout from "../Utilities/Logout";
import Dashboard from "./Home/Dashboard";
import Sidebar from "./Header/Sidebar";
import Category from "./Category/Category";
import CreateEntertainer from "./Entertainer/CreateEntertainer";
import Entertainer from "./Entertainer/Entertainer";
import Entertainerlist from "./Entertainer/Entertainerlist";
import EntertainerDetails from "./Entertainer/EntertainerDetails";
import UpdateEntertainer from "./Entertainer/UpdateEntertainer";
import GetYourEntertainer from "./Entertainer/GetYourEntertainer";
import Cart from "./Order/Cart";
import Order from "./Order/Order";
import Orders from "./Order/Orders";
import CreatePost from "./Post/CreatePost";
import UpdatePost from "./Post/UpdatePost";
import Post from "./Post/Post";
import Posts from "./Post/Posts";

export default function MainComponent() {
    return (
        <Routes>
            <Route exact="true" path="/" element={<Home />} />
            <Route
                exact="true"
                path="/dashboard"
                element={<Dashboard sidebar={<Sidebar />} />}
            />
            <Route exact="true" path="/login" element={<Login />} />
            <Route
                exact="true"
                path="/category"
                element={<Category sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/addentertainer/"
                element={<CreateEntertainer sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/entertainer/"
                element={<Entertainer sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/entertainerlist/"
                element={<Entertainerlist sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/entertainer/:id"
                element={<EntertainerDetails sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/updateentertainer/:id"
                element={<UpdateEntertainer sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/getyourentertainer/"
                element={<GetYourEntertainer sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/cart/"
                element={<Cart sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/order/"
                element={<Order sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/orders/"
                element={<Orders sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/createpost/"
                element={<CreatePost sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/updatepost/:id"
                element={<UpdatePost sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/post/"
                element={<Post sidebar={<Sidebar />} />}
            />
            <Route
                exact="true"
                path="/posts/"
                element={<Posts sidebar={<Sidebar />} />}
            />
            <Route exact="true" path="/logout" element={<Logout />} />
        </Routes>
    );
}
