import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Layout from "../Layout";
import {
    getCartById,
    deleteCardItem,
    createOrder,
    createReview,
    createInteraction,
} from "../../API/utilities";
import { userInfo } from "../../API/auth";
import { URL } from "../../API/url";

export default function Cart(props) {
    const [cart, setCart] = useState({});

    const [message, setMessage] = useState("");
    const token = userInfo().token;

    const handleDeleteCart = (id) => {
        deleteCardItem(id, token)
            .then((result) => {
                getCartById(token)
                    .then((result) => setCart(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => {
                console.log(err);
            });
    };

    const handleOrder = (id) => {
        const order = {
            refModel: cart.refModel,
            product: cart.product._id,
            price: cart.price,
            user: userInfo().decoded.id,
        };
        createOrder(order, token)
            .then((result) => {
                setMessage("your order received!");
                createReview({ entertainerId: cart.product._id }, token)
                    .then(() => console.log("successfully reviewed."))
                    .catch((error) => console.log(error));
                createInteraction(
                    {
                        userId: userInfo().decoded.id,
                        productInteraction: [{ product: cart.product._id }],
                    },
                    token
                )
                    .then(() => console.log("successfully counted."))
                    .catch((error) => console.log(error));
                handleDeleteCart(cart._id);
            })
            .catch((err) => setMessage("failed to receive order!"));
    };

    useEffect(() => {
        getCartById(userInfo().token)
            .then((result) => setCart(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Cart">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12 p-5">
                    <h1>Your Cart</h1>
                    {message && (
                        <div className="alert alert-success" role="alert">
                            {message}
                        </div>
                    )}
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        <table className="table table-hover">
                            <thead>
                                <tr>
                                    <th scope="col" width="15%">
                                        #
                                    </th>
                                    <th scope="col">Image</th>
                                    <th scope="col">Product Name</th>
                                    <th scop="col">Remove</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td scope="row">{cart._id}</td>
                                    <td>
                                        {cart.product &&
                                            cart.refModel === "entertainer" && (
                                                <img
                                                    className="img-fluid"
                                                    style={{
                                                        height: "50px",
                                                        width: "40px",
                                                    }}
                                                    src={`${URL}/entertainer/getProfile/${cart.product._id}`}
                                                    alt=""
                                                />
                                            )}
                                        {cart.product &&
                                            cart.refModel === "product" && (
                                                <img
                                                    className="img-fluid"
                                                    style={{
                                                        height: "50px",
                                                        width: "40px",
                                                    }}
                                                    src={`${URL}/product/getPhoto/${cart.product._id}`}
                                                    alt=""
                                                />
                                            )}
                                    </td>
                                    {cart.product && (
                                        <td>
                                            <Link
                                                className="text-decoration-none fw-bold text-dark"
                                                to={`/${cart.refModel}/${cart.product._id}`}
                                            >
                                                {cart.product.name}
                                            </Link>
                                        </td>
                                    )}
                                    <td>
                                        <button
                                            onClick={() =>
                                                handleDeleteCart(cart._id)
                                            }
                                            className="btn btn-danger btn-sm"
                                        >
                                            Remove From Cart
                                        </button>
                                    </td>
                                </tr>
                                <tr>
                                    <th scope="row" />
                                    <td colSpan={4} className="text-right">
                                        <button
                                            onClick={handleOrder}
                                            className="btn btn-success mr-4"
                                        >
                                            Proceed To Checkout
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
