import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import { userInfo } from "../../API/auth";
import { updatePost, getPostById } from "../../API/utilities";
import { useParams } from "react-router-dom";

export default function UpdatePost(props) {
    const [values, setValues] = useState({
        message: "",
        desc: "",
    });
    const id = useParams().id;

    const { message, desc } = values;

    const formHandler = (e) => {
        const value =
            e.target.name === "photos" ? e.target.files : e.target.value;

        setValues({
            ...values,
            [e.target.name]: value,
        });
    };

    const submitHandler = (e) => {
        e.preventDefault();
        const formData = new FormData(e.target);
        updatePost(id, formData, userInfo().token)
            .then((result) => {
                setValues({
                    ...values,
                    message: "post successfully updated!",
                    name: "",
                    desc: "",
                });
            })
            .catch((err) => {
                setValues({ ...values, message: "something weng wrong!" });
            });
    };

    useEffect(() => {
        getPostById(id, userInfo().token)
            .then((result) =>
                setValues({
                    ...values,
                    desc: result.data[0].desc,
                })
            )
            .catch((err) => console.log(err));
    }, []);

    return (
        <Layout title="Create New Post">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Update Post</h1>
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {message && (
                            <div className="alert alert-success" role="alert">
                                {message}
                            </div>
                        )}
                        <form onSubmit={submitHandler}>
                            <div className="form-group mb-1">
                                <label for="description">Description</label>
                                <textarea
                                    className="form-control"
                                    id="description"
                                    name="desc"
                                    value={desc}
                                    onChange={formHandler}
                                    placeholder="Enter description"
                                ></textarea>
                            </div>
                            <div className="form-group mb-1">
                                <label for="photo">Photos</label>
                                <input
                                    type="file"
                                    className="form-control-file"
                                    id="photo"
                                    name="photos"
                                    multiple
                                    onChange={formHandler}
                                />
                            </div>
                            <button type="submit" className="btn btn-primary">
                                Submit
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
