import React, { useState } from "react";
import Layout from "../Layout";
import { userInfo } from "../../API/auth";
import { createPost } from "../../API/utilities";

export default function CreatePost(props) {
    const [values, setValues] = useState({
        message: "",
        desc: "",
        formData: new FormData(),
    });

    const { message, desc, formData } = values;
    formData.set("user", userInfo().decoded.id);

    const formHandler = (e) => {
        const value =
            e.target.name === "photos" ? e.target.files : e.target.value;

        if (e.target.name === "photos") {
            for (let i in value) {
                formData.append(e.target.name, value[i]);
            }
        } else {
            formData.set(e.target.name, value);
        }

        setValues({
            ...values,
            [e.target.name]: e.target.value,
        });
    };

    const submitHandler = (e) => {
        e.preventDefault();
        createPost(formData, userInfo().token)
            .then((result) => {
                setValues({
                    ...values,
                    message: "post successfully added!",
                    desc: "",
                });
            })
            .catch((err) => {
                setValues({ ...values, message: "something weng wrong!" });
            });
    };

    return (
        <Layout title="Create New Post">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Create Post</h1>
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {message && (
                            <div className="alert alert-success" role="alert">
                                {message}
                            </div>
                        )}
                        <form onSubmit={submitHandler}>
                            <div className="form-group mb-1">
                                <label for="description">Description</label>
                                <textarea
                                    className="form-control"
                                    id="description"
                                    name="desc"
                                    value={desc}
                                    onChange={formHandler}
                                    placeholder="Enter description"
                                ></textarea>
                            </div>
                            <div className="form-group mb-1">
                                <label for="photo">Photos</label>
                                <input
                                    type="file"
                                    className="form-control-file"
                                    id="photo"
                                    name="photos"
                                    multiple
                                    onChange={formHandler}
                                />
                            </div>
                            <button type="submit" className="btn btn-primary">
                                Submit
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
