import React, { useEffect, useState } from "react";
import Layout from "../Layout";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleInfo, faTrash } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";
import { userInfo } from "../../API/auth";
import { getEntertainer, deleteEntertainer } from "../../API/utilities";

export default function Entertainerlist(props) {
    const [entertainers, setEntertainers] = useState([]);
    const [message, setMessage] = useState(false);

    const deleteHandler = (id) => {
        deleteEntertainer(id, userInfo().token)
            .then((result) => {
                setMessage("entertainer deleted successfully!");
                getEntertainer(userInfo().token)
                    .then((result) => setEntertainers(result.data))
                    .catch((err) => console.log(err));
            })
            .catch((err) => setMessage("entertainer delete failed!"));
    };

    useEffect(() => {
        getEntertainer(userInfo().token)
            .then((result) => setEntertainers(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Entertainer List">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Entertainer List</h1>
                    {message && (
                        <div className="alert alert-success" role="alert">
                            {message}
                        </div>
                    )}
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        <ul className="list-group">
                            {entertainers.map((item) => (
                                <li
                                    key={item._id}
                                    className="list-group-item d-flex justify-content-between align-items-center my-1"
                                >
                                    <Link
                                        className="nav-link"
                                        to={`/entertainer/${item._id}/`}
                                    >
                                        {item.username}
                                    </Link>
                                    <div>
                                        <Link
                                            to={`/updateentertainer/${item._id}/`}
                                            className="btn btn-dark btn-sm m-1"
                                        >
                                            <FontAwesomeIcon
                                                icon={faCircleInfo}
                                            />
                                        </Link>
                                        <button
                                            type="button"
                                            className="btn btn-danger btn-sm m-1 "
                                            onClick={() =>
                                                deleteHandler(item._id)
                                            }
                                        >
                                            <FontAwesomeIcon icon={faTrash} />
                                        </button>
                                    </div>
                                </li>
                            ))}
                        </ul>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
