import React, { useEffect, useState } from "react";
import ReactPaginate from "react-paginate";
import Layout from "../Layout";
import { Link } from "react-router-dom";
import { URL } from "../../API/url";
import { userInfo } from "../../API/auth";
import { getEntertainer, getFilterEntertainer } from "../../API/utilities";
import Filter from "../../Utilities/Filter";
import "./Entertainer.css";

const Items = ({ currentItems }) => {
    return (
        <>
            {currentItems &&
                currentItems.map((item) => (
                    <div
                        key={item._id}
                        style={{
                            background: "none",
                            position: "relative",
                        }}
                        className="col-4 card border-0"
                    >
                        <Link
                            className="nav-link"
                            to={`/entertainer/${item._id}`}
                        >
                            <div
                                style={{
                                    width: 100,
                                    height: 100,
                                    left: 40,
                                    top: -60,
                                    position: "absolute",
                                    borderRadius: 100,
                                    overflow: "hidden",
                                }}
                            >
                                <img
                                    className="img-fluid"
                                    src={`${URL}/entertainer/getProfile/${item._id}`}
                                    alt=""
                                />
                            </div>

                            <div
                                style={{ background: "#D9D9D9" }}
                                className="card-body pt-5 rounded-4"
                            >
                                <h6 className="text-capitalize fw-semibold">
                                    {item.name}
                                </h6>
                                <p>{item.username}</p>
                            </div>
                        </Link>
                    </div>
                ))}
        </>
    );
};

export default function Entertainer(props) {
    const [entertainers, setEntertainers] = useState([]);
    const itemPerPage = 6;
    // Here we use item offsets; we could also use page offsets
    // following the API or data you're working with.
    const [itemOffset, setItemOffset] = useState(0);
    // Simulate fetching items from another resources.
    // (This could be items from props; or items loaded in a local state
    // from an API endpoint with useEffect and useState)
    const endOffset = itemOffset + itemPerPage;
    const currentItems = entertainers.slice(itemOffset, endOffset);
    const pageCount = Math.ceil(entertainers.length / itemPerPage);

    // Invoke when user click to request another page.
    const handlePageClick = (event) => {
        const newOffset = (event.selected * itemPerPage) % entertainers.length;
        setItemOffset(newOffset);
    };

    const handleFilter = (data) => {
        getFilterEntertainer(userInfo().token, data)
            .then((result) => setEntertainers(result.data))
            .catch((err) => console.log(err));
    };

    const searchedValue = (values) => {
        setEntertainers(values);
    };

    useEffect(() => {
        getEntertainer(userInfo().token)
            .then((result) => setEntertainers(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Entertainers">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <div
                        style={{
                            paddingTop: "5rem",
                            paddingBottom: "5rem",
                        }}
                        className="row"
                    >
                        <div className="col-md-3">
                            <Filter
                                handleFilter={handleFilter}
                                searchedValue={searchedValue}
                            />
                        </div>
                        <div className="col-md-9">
                            <div className="row my-3">
                                <Items currentItems={currentItems} />
                            </div>

                            <ReactPaginate
                                breakLabel="..."
                                nextLabel="next >"
                                onPageChange={handlePageClick}
                                pageRangeDisplayed={5}
                                pageCount={pageCount}
                                previousLabel="< previous"
                                renderOnZeroPageCount={null}
                                containerClassName="pagination"
                                activeClassName="active"
                                previousClassName="pagination-previous"
                                nextClassName="pagination-next"
                                breakClassName="pagination-break"
                                disabledClassName="pagination-disabled"
                                pageClassName="pagination-page"
                                pageLinkClassName="pagination-link"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
