import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Layout from "../Layout";
import { getOrderById } from "../../API/utilities";
import { userInfo } from "../../API/auth";

export default function Order(props) {
    const [order, setOrder] = useState([]);

    useEffect(() => {
        getOrderById(userInfo().token)
            .then((result) => setOrder(result.data))
            .catch((err) => console.log(err));
    }, []);
    return (
        <Layout title="Order">
            <div className="row">
                <div className="col-md-3 col-12 px-0">{props.sidebar}</div>
                <div className="col-md-9 col-12">
                    <h1>Order</h1>
                    <div
                        style={{ paddingTop: "3rem", paddingBottom: "7rem" }}
                        className="container"
                    >
                        {order.map((item) =>
                            item.done === false ? (
                                <div className="card my-1" key={item._id}>
                                    <div className="card-body">
                                        <p>
                                            <span className="fw-bold">
                                                Order ID:
                                            </span>{" "}
                                            {item._id}
                                        </p>
                                        <p>
                                            <span className="fw-bold">
                                                Customer Email:
                                            </span>{" "}
                                            {item.user.email}
                                        </p>
                                        <p>
                                            <span className="fw-bold">
                                                Entertainer Name:
                                            </span>{" "}
                                            <Link
                                                className="text-decoration-none text-dark fw-bold"
                                                to={`/${item.refModel}/${item.product._id}`}
                                            >
                                                {item.product.name}
                                            </Link>
                                        </p>
                                        <p>
                                            <span className="fw-bold">
                                                Order Time:
                                            </span>{" "}
                                            {item.time}
                                        </p>
                                        <p>
                                            <span className="fw-bold">
                                                Order Status:
                                            </span>{" "}
                                            {item.done === false
                                                ? "Pending"
                                                : "Done"}
                                        </p>
                                    </div>
                                </div>
                            ) : (
                                ""
                            )
                        )}
                    </div>
                </div>
            </div>
        </Layout>
    );
}
